import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { UserSession } from 'blockstack'
import { appConfig, OTHER_COUNCILS } from './constants'
import { loadRuler, loadSubjects } from './utils'

class OtherCouncils extends Component {

  constructor(props) {
    super(props)
    this.state = {
      councils: []
    }
    this.userSession = new UserSession({ appConfig })
    this.resolveCouncils = this.resolveCouncils.bind(this)
  }

  componentWillMount() {
    this.resolveCouncils()
  }

  resolveCouncils() {
    const councils = this.state.councils
    OTHER_COUNCILS.map((council, index) => {
      return loadRuler(this.userSession, council.ruler, council.app)
      .then(ruler => {
        councils[index] = {
          ruler: {
            username: council.ruler,
            data: ruler
          },
          subjects: [],
          app: council.app
        }

        this.setState({ councils })
        return loadSubjects(this.userSession, council.ruler, council.app)
        .then(subjects => {
            councils[index].subjects = subjects
            this.setState({ subjects })
        })
        .catch(error => {
          console.log('problem loading subjects')
          console.log(error)
          councils[index].subjects = []
          councils[index].error = true
          this.setState({ council })
        })
      })
      .catch((error) => {
        console.log('ruler not found')
        console.log(error)
        councils[index] = {
          error: true,
          ruler: {
            username: council.ruler,
            data: null,
          },
          subjects: [],
          app: council.app
        }
      })
    })
  }

  render() {
    const councils = this.state.councils
    return (
      <div className="OtherCouncils container">
          <h2>Other Councils</h2>
          <div className="list-group">
          {councils.length === 0 ?
            <div
              className="list-group-item list-group-item-action flex-column align-items-start"
            >
              <div class="d-flex w-100 justify-content-between">
                <h5 class="mb-1">Loading other Councils...</h5>
              </div>
              <p class="mb-1">&nbsp;</p>
            </div>
            :
            <div>
            {councils.map((council, index) => {
              const protocol = council.app.split('//')[0]
              const hostname = council.app.split('//')[1]
              const planet = council.app
              const ruler = council.error ? '' : council.ruler
              const data = ruler ? ruler.data : ''
              const animal = data ? data.animal : ''
              if (council.error) {
                return (
                  <div
                    className="list-group-item list-group-item-action flex-column align-items-start"
                    key={index}
                  >
                    <div class="d-flex w-100 justify-content-between">
                      <h5 class="mb-1">{ council.ruler.username }'s council can't be reached</h5>
                    </div>
                    <p class="mb-1">{ planet }</p>
                  </div>
                )
              } else {
                return (
                  <Link
                    className="list-group-item list-group-item-action flex-column align-items-start"
                    to={`/council/${protocol}/${hostname}/${council.ruler.username}`}
                    key={index}
                  >
                    <div class="d-flex w-100 justify-content-between">
                      <h5 class="mb-1">{ council.ruler.username } { animal ? animal.name : '' }</h5>
                      <span class="badge badge-primary badge-pill" title="Subjects">{ council.subjects.length }</span>
                    </div>
                    <p class="mb-1">{ planet }</p>
                  </Link>
                )
              }
            })}
            </div>
          }
          </div>
      </div>
    );
  }
}

export default OtherCouncils